
import { getCount, SCORE, Attempt } from "@/gameplay";
import { defineComponent, PropType } from "@vue/runtime-core";

export default defineComponent({
  props: {
    number: {
      type: Number,
      required: true,
    },
    attempt: {
      type: Object as PropType<Attempt>,
      required: true,
    },
    assist: {
      type: Boolean,
      default: false,
    },
  },

  setup() {
    const hints = [SCORE.MATCH, SCORE.CLOSE_MATCH];

    return {
      hints,
      getCount,
    };
  },
});
